
// Import Upbase
@import "upbase/scss/upbase";

// Project styles go here

//Variables

//Colors
$blue: #003DFF;
$periwinkle: #EEF1FA;
$periwinkle-darker: #C6D3FE;
$yellow: #FEEE01;

body {
	margin: 0;
	color: $blue;
}

h1 {
	font-family: 'Work Sans', sans-serif;
	font-size: 18pt;
    font-weight: 400;
    letter-spacing: 1px;
    line-height: 1.25;
}

h2 {
	font-family: 'Work Sans', sans-serif;
	font-size: 16pt;
    font-weight: 700;
    line-height: 1.25;
}

h3 {
	font-family: 'Work Sans', sans-serif;
	font-size: 14pt;
    font-weight: 700;
    line-height: 1.25;	
}

h4 {
	margin: 0 auto;
	width: 100px;
    padding-bottom: 5pt;
	font-family: 'Inconsolata';
    font-weight: 700;
    font-size: 18pt;
    text-align: center;
    border-bottom: 2pt solid $yellow;
}

p {
	font-family: 'Work Sans', sans-serif;
	font-size: 14pt;
    font-weight: 400;
    line-height: 1.25;	
}

.block__1, .block__3 {
	display: inline-block;
    width: 100%;
    background-color: $periwinkle;
}

.block__1__content {
	width: 100%;
	max-width: 1000px;
	margin: 0 auto;
	background-image: url("/static/img/phone_screen_dashboard.png");
	background-repeat: no-repeat;
    background-position: 90% 50px;
}

.block__1__intro {
	float: left;
	max-width: 380px;
	margin-left: 11%;
    margin-top: 50px;
}

.block__1__intro__p {
	margin: 50px 0;
}

.email-signup {
	max-width: 380px;
	margin-left: 11%;
}

.email-signup__input {
	width: 100%;
    max-width: 340px;
    height: 50px;
    border: 2pt solid $blue;
    padding: 0 20px;
    font-family: 'Inconsolata';
    font-size: 14pt;
    color: $blue;
    transition: .1s ease-in-out;

    &:hover {
    	background-color: $periwinkle;
    }

    &:focus {
    	outline: none;
    	background-color: $periwinkle-darker;
    }
}

.email-signup__button {
	width: 58%;
    height: 50px;
    margin: 20px 21% 100px;
    border-radius: 25px;
    border: 2pt solid $blue;
    color: $blue;
    background-color: $yellow;
    font-family: 'Inconsolata';
    font-weight: 700;
    font-size: 14pt;
    text-transform: uppercase;
    letter-spacing: 1pt;
    box-shadow: 0 3px 0 $periwinkle-darker;
    transition: .05s linear;
    cursor: pointer;

    &:hover {
    	transform: translateY(1px);
    	box-shadow: 0 2px 0 $periwinkle-darker;
    }

    &:focus {
    	outline: none;
    	transform: translateY(2px);
    	box-shadow: none;
    }
}

.block__2 {
	padding: 60px 0;
	border-top: 2pt solid $blue;
	border-bottom: 2pt solid $blue;
}

.block__2__content {
	width: 60%;
	max-width: 1000px;
	margin: 0 auto;
}

.block__3 {
	padding: 100px 0;
}

.block__3__content {
	width: 100%;
	max-width: 1000px;
	margin: 0 auto;
}

.card {
	width: 40%;
    max-width: 420px;
    background-color: white;
    border: 2pt solid #003DFF;
    padding: 20px;
    float: right;
    clear: both;
    margin: 30px 11% 0 0;
}

.phone-screenshot {
	margin-left: 11%;
}